import { withRouter } from 'next/router';

const Trips = ({ router }) => {
  const { count } = router.query;
  return (
    <>
      <h1>my trips</h1>
      <button
        onClick={() => {
          router.push({
            pathname: 'my-allegiant/about',
            query: { count: count + 1 },
          });
        }}
      >
        +1
      </button>
      {count && <p>{`Count = ${count}`}</p>}
    </>
  );
};

export default withRouter(Trips);
